import GlobalStyle from "./components/GlobalStyle";
import Company from "./pages/Company";
import Contact from "./pages/Contact";
import Apps from "./pages/Apps";
import Nav from "./components/Nav";

import { Switch, Route, Redirect } from "react-router-dom";

function App() {
  const reload = () => window.location.reload();

  return (
    <div className="App">
      <GlobalStyle />
      <Nav />

      <Switch>
        <Route path="/" exact>
          <Apps />
        </Route>
        <Route path="/company">
          <Company />
        </Route>

        <Route path="/contact">
          <Contact />
        </Route>

        <Route path="/support">
          <Contact />
        </Route>

        <Route path="/terms-of-service/" onEnter={reload} />
        <Route path="/privacy-policy/" onEnter={reload} />
      </Switch>
    </div>
  );
}

export default App;
