import React from "react";

// Styling and animation
import styles from "styled-components";
import { motion } from "framer-motion";
import styled from "styled-components";
import { Link } from "react-router-dom";

import appStoreLogo from "../img/Download_on_the_App_Store_Badge_US-UK_RGB_blk_092917.svg";

const AppEntry = ({ name, description, image, link, linkBody }) => {
  console.log(link);
  return (
    <StyledAppEntry>
      <h3>{name}</h3>
      <div className="description">
        <p>{description}</p>
        {link && link.startsWith("https://apps.apple.com") && (
          <a href={link}>
            <img src={appStoreLogo} />
          </a>
        )}
        {link && linkBody && (
          <a href={link}>
            <p>{linkBody}</p>
          </a>
        )}
      </div>
      <img className="screenshot" src={image} />
    </StyledAppEntry>
  );
};

const StyledAppEntry = styled(motion.div)`
  min-height: 30vh;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);
  text-align: center;
  border-radius: 1rem;
  overflow: hidden;
  padding: 1rem;

  .description {
    a {
      color: #333;
    }
    img {
      width: 150px;
      height: 75px;
      object-fit: fill;
    }
  }
  h3 {
    color: #333;
  }

  p {
    padding: 1rem 0rem;
    font-size: 1.3rem;
    color: #605f5f;
  }

  .screenshot {
    height: 30vh;
    width: 100%;
    object-fit: cover;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);
    vertical-align: bottom;
  }
`;

export default AppEntry;
